
#app {
  background-color: black;
  width: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.window {
  width: 800px;
  height: 600px;
  border: 1px solid rgba(255, 255, 255, 0.171);
  position: relative;
  overflow: hidden;
}
.bg {
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  position: absolute;
}
.video {
  position: absolute;
  left: 410px;
  top: 140px;
  width: 300px;
  height: 200px;
  outline: none !important;
}
.video:focus {
  outline: none !important;
}
.touch_field {
  position: absolute;
  cursor: pointer;
}
